import FormElement from "./elements/form-element";
import FormLabel from "./elements/form-label";
import FormTextbox from "./elements/form-textbox";
import FormCommonInfo from "./elements/form-commoninfo";
import FormImage from "./elements/form-image";
import FormImageData from "./elements/form-image-data";

import Constants from "./constants";
import GlobalVars from "./globalvars";

/**
 * FormViewer object with controller for whole form.
 *
 * @module formviewer
 */

 export default class FormViewer {
    constructor(containerDiv, formInfo) {
        this._container = containerDiv;

        //common
        this._title = '';
        this._priority = 0;

        //pages
        this._lstPages = [];

        //elements
        this._lstElements = [];
        this._nextIdx = 0;

        //align
        this._horzAlign = null;
        this._vertAlign = null;

        //selection
        this._isMultiSelect = false;
        this._selectionCtrl = null;
        this._startSelectPos = null;
        this._curSelection = null;
        this._lstSelectedElems = [];

        //current styles (last used)
        this._curStyles = {};
        Object.assign(this._curStyles, Constants.DEFAULT_STYLES);

        //container scroll & position
        this._originalOffset = {};

        //some UI names
        this._selectOTsName = '';
        this._selectCommonInfosName = '';
        this._selectImageOptsName = '';

        if(formInfo != null)
            this.init(formInfo);
        else 
            this._initEmpty(); //empty form
    }

    init(formInfo) {
        //common form info
        Object.assign(this._originalOffset, this._container.offset());

        this._title = formInfo.name;
        this._priority = formInfo.priority;

        //pages
        if(formInfo.pages != undefined && formInfo.pages.length > 0) {
            for(let i = 0; i < formInfo.pages.length; i++) {
                let page = formInfo.pages[i];
                this._lstPages.push({
                    order: page.order,
                    width: page.width,
                    height: page.height
                });
            }
        }
        else { //init first page
            this._initFirstPage();
        }

        //elements
        if(formInfo.elements != undefined) {
            let arrElements = JSON.parse(formInfo.elements);
            if(arrElements != undefined && arrElements.length > 0) {
                let len = arrElements.length;
                for(let i = 0; i < len; i++) {
                    let srcElem = arrElements[i];
                    let newElem = this.createFormElement(srcElem.type, srcElem, i);
                    this._lstElements.push(newElem);
                }
                this._nextIdx = len;
            }
        }        

        //old compatibility elements
        if(formInfo.labels != undefined && formInfo.labels.length > 0) {
            let len = formInfo.labels.length;
            for(let i = 0; i < len; i++) {
                let srcElem = formInfo.labels[i];
                let newElem = this.createFormElement(Constants.ELEMENT_TYPE.LABEL, srcElem, i); //new FormLabel(srcElem, this, i);
                this._lstElements.push(newElem);
            }
            this._nextIdx = len;
        }

        if(formInfo.formElms != undefined && formInfo.formElms.length > 0) {
            let len = formInfo.formElms.length;
            for(let i = 0; i < len; i++) {
                let srcElem = formInfo.formElms[i];
                let newElem = this.createFormElement(Constants.ELEMENT_TYPE.TEXTBOX, srcElem, this._nextIdx + i);
                this._lstElements.push(newElem);
            }
            this._nextIdx += len;
        }

        if(formInfo.commonInfos != undefined && formInfo.commonInfos.length > 0) {
            let len = formInfo.commonInfos.length;
            for(let i = 0; i < len; i++) {
                let srcElem = formInfo.commonInfos[i];
                let newElem = this.createFormElement(Constants.ELEMENT_TYPE.COMMON_INFO, srcElem, this._nextIdx + i);
                this._lstElements.push(newElem);
            }
            this._nextIdx += len;
        }
    }

    _initEmpty() {
        this._initFirstPage();
    }

    _initFirstPage() {
        let page = {
            order: 0,
            width: Constants.DEFAULT_PAGE_WIDTH,
            height: Constants.DEFAULT_PAGE_HEIGHT
        };
        this._lstPages.push(page);
    }

    createFormElement(type, elementInfo, idx) {
        let elem;
        if(type === Constants.ELEMENT_TYPE.LABEL)
            elem = new FormLabel(elementInfo, this, idx);
        else if(type === Constants.ELEMENT_TYPE.TEXTBOX)
            elem = new FormTextbox(elementInfo, this, idx);
        else if(type === Constants.ELEMENT_TYPE.COMMON_INFO)
            elem = new FormCommonInfo(elementInfo, this, idx);
        else if(type === Constants.ELEMENT_TYPE.IMAGE)
            elem = new FormImage(elementInfo, this, idx);
        else if(type === Constants.ELEMENT_TYPE.IMAGE_DATA)
            elem = new FormImageData(elementInfo, this, idx);

        return elem;
    }

    dispose() {
        //release resources here
        this._container = null;
        this._lstElements = [];
    }

    addElement(type) {
        console.log('addElement, type: ' + type);
        // console.log('offset:', this._container.offset());
        // console.log('scrollTop:', this._container.scrollTop());
        // console.log('width:', this._container.width());

        let curOffset = this._container.offset();
        let deltaLeft = curOffset.left - this._originalOffset.left; //equal 0 or less than 0
        let deltaTop = curOffset.top - this._originalOffset.top; //equal 0 or less than 0

        let center = Math.round(this._container.width() / 2);
        let w = 120;
        let h = (type != Constants.ELEMENT_TYPE.IMAGE && type != Constants.ELEMENT_TYPE.IMAGE_DATA) ? 32 : 120;

        let elementInfo = {
            left: center - w - deltaLeft,
            top: 200 - deltaTop,
            width: w,
            height: h
        };
        elementInfo.formatStyle = {};
        Object.assign(elementInfo.formatStyle, this._curStyles);

        //element default value
        this.setDefaultElementValue(type, elementInfo);

        let newElem = this.createFormElement(type, elementInfo, this._nextIdx++);
        this._lstElements.push(newElem);

        newElem.render4Editing(this._container);

        this.unSelectAll();
        this.selectElement(newElem);
        newElem.onSelect(null);
    }

    removeElement() {

    }

    render4Editing(containerDiv) {
        this._container = containerDiv;
        this._container.html('');
        let len = this._lstElements.length;
        for(let i = 0; i < len; i++) {
            this._lstElements[i].render4Editing(this._container);
        }

        //alignment lines
        this._horzAlign = $('<div>').addClass('line-horz');
        this._vertAlign = $('<div>').addClass('line-vert');
        this._container.append(this._horzAlign);
        this._container.append(this._vertAlign);

        this._selectionCtrl = $('<div>').addClass('multi-selecting');
        this._container.append(this._selectionCtrl);

        //common events
        var _self = this;
        this._container.off('mousedown')
            // .on('click', function(ev) {
            //     if(_self._isMultiSelect)
            //         return;

            //     console.log('main div clicked');
            //     _self.unSelectAll();
            // })
            .on('mousedown', function(ev) {
                console.log('main div mousedown');
                _self.onMouseDown(ev);
                ev.preventDefault();
            });

        //form info UI
        $('#txtFormTitle').val(this._title);
        $('#txtPriority').val(this._priority);
    }

    render4Input(containerDiv) {
        containerDiv.html('');
        let len = this._lstElements.length;
        for(let i = 0; i < len; i++) {
            this._lstElements[i].render4Input(containerDiv);
        }
    }

    render4Printing(containerDiv) {
        containerDiv.html('');
        let len = this._lstElements.length;
        for(let i = 0; i < len; i++) {
            this._lstElements[i].render4Printing(containerDiv);
        }
    }

    selectElement(elem) {
        if(elem == undefined || elem == null)
            return;

        //check if already selecting
        let len = this._lstSelectedElems.length;
        for(let i = 0; i < len; i++) {
            if(this._lstSelectedElems[i]._idx == elem._idx)
                return;
        }

        this._lstSelectedElems.push(elem);

        //event updating UI
        this.updateUIElementData();
    }

    unSelectAll() {
        this._lstSelectedElems = [];
        let len = this._lstElements.length;
        for(let i = 0; i < len; i++) {
            this._lstElements[i].onUnSelect();
        }
    }

    alignSelectingElemsLeft() {
        //get min left
        let minLeft = 9999;

        let len = this._lstSelectedElems.length;
        if(len == 0)
            return;
        
        for(let i = 0; i < len; i++) {
            if(this._lstSelectedElems[i]._bounding.left < minLeft)
                minLeft = this._lstSelectedElems[i]._bounding.left;
        }

        //update all selected element
        for(let i = 0; i < len; i++) {
            let elem = this._lstSelectedElems[i];
            if(elem._bounding.left != minLeft) {
                elem.onMoveInactive({
                    x: minLeft - elem._bounding.left,
                    y: 0
                });
            }
        }
    }

    alignSelectingElemsRight() {
        //get max right
        let maxRight = -1;

        let len = this._lstSelectedElems.length;
        if(len == 0)
            return;
        
        for(let i = 0; i < len; i++) {
            let right = this._lstSelectedElems[i]._bounding.left + this._lstSelectedElems[i]._bounding.width;
            if(right > maxRight)
                maxRight = right;
        }

        //update all selected element
        for(let i = 0; i < len; i++) {
            let elem = this._lstSelectedElems[i];
            if(elem._bounding.left + elem._bounding.width != maxRight) {
                elem.onMoveInactive({
                    x: maxRight - elem._bounding.left - elem._bounding.width,
                    y: 0
                });
            }
        }
    }

    alignSelectingElemsTop() {
        //get min left
        let minTop = 9999;

        let len = this._lstSelectedElems.length;
        if(len == 0)
            return;
        
        for(let i = 0; i < len; i++) {
            if(this._lstSelectedElems[i]._bounding.top < minTop)
                minTop = this._lstSelectedElems[i]._bounding.top;
        }

        //update all selected element
        for(let i = 0; i < len; i++) {
            let elem = this._lstSelectedElems[i];
            if(elem._bounding.top != minTop) {
                elem.onMoveInactive({
                    x: 0,
                    y: minTop - elem._bounding.top
                });
            }
        }
    }

    alignSelectingElemsBottom() {
        //get max right
        let maxBottom = -1;

        let len = this._lstSelectedElems.length;
        if(len == 0)
            return;
        
        for(let i = 0; i < len; i++) {
            let bottom = this._lstSelectedElems[i]._bounding.top + this._lstSelectedElems[i]._bounding.height;
            if(bottom > maxBottom)
                maxBottom = bottom;
        }

        //update all selected element
        for(let i = 0; i < len; i++) {
            let elem = this._lstSelectedElems[i];
            if(elem._bounding.top + elem._bounding.height != maxBottom) {
                elem.onMoveInactive({
                    x: 0,
                    y: maxBottom - elem._bounding.top - elem._bounding.height
                });
            }
        }
    }

    //check & show align if suitable
    checkAlignment(srcElement, toLeft, toBottom, ignoreSelecting) {
        // console.log('checkAlignment, ' + toLeft + ', ' + toBottom);
        // console.log('srcElement bounding: (' + srcElement._bounding.left + ', ' + srcElement._bounding.top + ', ' + srcElement._bounding.width + ', ' + srcElement._bounding.height + ')');
        //check bounding with other elements
        let EPS = 5; //epsilon spacing
        let EPS2 = EPS * 2 + 1; //double epsilon spacing

        let left = Math.round(srcElement._bounding.left);
        let right = Math.round(srcElement._bounding.left + srcElement._bounding.width);
        let top = Math.round(srcElement._bounding.top);
        let bottom = Math.round(srcElement._bounding.top + srcElement._bounding.height);

        let leftAligns = new Array(EPS2);
        let rightAligns = new Array(EPS2);
        let topAligns = new Array(EPS2);
        let bottomAligns = new Array(EPS2);
        for(let i = 0; i < EPS2; i++) {
            leftAligns[i] = 0;
            rightAligns[i] = 0;
            topAligns[i] = 0;
            bottomAligns[i] = 0;
        }

        let tmp;
        let len = this._lstElements.length;
        for(let i = 0; i < len; i++) {
            let elem = this._lstElements[i];
            if(elem._idx == srcElement._idx || (ignoreSelecting && elem._isSelected))
                continue;

            // console.log('elem bounding: (' + elem._bounding.left + ', ' + elem._bounding.top + ', ' + elem._bounding.width + ', ' + elem._bounding.height + ')');
            if(Math.abs(elem._bounding.left - left) <= EPS) {
                // console.log('here 1');
                tmp = Math.round(elem._bounding.left) - left + EPS;
                leftAligns[tmp]++;
            }

            if(Math.abs(elem._bounding.top - top) <= EPS) {
                // console.log('here 2');
                tmp = Math.round(elem._bounding.top) - top + EPS;
                topAligns[tmp]++;
            }

            if(Math.abs(elem._bounding.left + elem._bounding.width - right) <= EPS) {
                // console.log('here 3');
                tmp = Math.round(elem._bounding.left + elem._bounding.width) - right + EPS;
                rightAligns[tmp]++;
            }

            if(Math.abs(elem._bounding.top + elem._bounding.height - bottom) <= EPS) {
                // console.log('here 4');
                tmp = Math.round(elem._bounding.top + elem._bounding.height) - bottom + EPS;
                bottomAligns[tmp]++;
            }
        }

        //find max count for each direction
        let maxHorz = 0, maxVert = 0, maxHorz2 = 0, maxVert2 = 0;
        let maxHorzIdx = -1, maxVertIdx = -1, maxHorzIdx2 = -1, maxVertIdx2 = -1;
        for(let i = 0; i < EPS2; i++) {
            if(leftAligns[i] > 0 && ((toLeft && i < EPS)||(!toLeft && i > EPS)))
                leftAligns[i]++;
            if(leftAligns[i] > maxVert) {
                maxVert = leftAligns[i];
                maxVertIdx = i;
            }

            if(rightAligns[i] > 0 && ((toLeft && i < EPS)||(!toLeft && i > EPS)))
                rightAligns[i]++;
            if(rightAligns[i] > maxVert2) {
                maxVert2 = rightAligns[i];
                maxVertIdx2 = i;
            }

            if(topAligns[i] > 0 && ((!toBottom && i < EPS)||(toBottom && i > EPS)))
                topAligns[i]++;
            if(topAligns[i] > maxHorz) {
                maxHorz = topAligns[i];
                maxHorzIdx = i;
            }

            if(bottomAligns[i] > 0 && ((!toBottom && i < EPS)||(toBottom && i > EPS)))
                bottomAligns[i]++;
            if(bottomAligns[i] > maxHorz2) {
                maxHorz2 = bottomAligns[i];
                maxHorzIdx2 = i;
            }
        }

        //horz
        let ret = {
            x: -100,
            y: -100
        };
        let eps = 3;
        if(maxHorz > 0 || maxHorz2 > 0) {
            if(maxHorz >= maxHorz2) {
                tmp = top - EPS + maxHorzIdx;
                this._horzAlign.css({ top: tmp + 'px' });
                ret.y = tmp - top;
            }
            else {
                tmp = bottom - EPS + maxHorzIdx2;
                this._horzAlign.css({ top: tmp + 'px' });
                ret.y = tmp - bottom;
            }
            
            if(Math.abs(ret.y) < eps) 
                this._horzAlign.addClass('line-correct');
            else
                this._horzAlign.removeClass('line-correct');
        }
        else {
            this._horzAlign.css({ top: '-500px' });
        }

        //vert
        if(maxVert > 0 || maxVert2 > 0) {
            if(maxVert >= maxVert2) {
                tmp = left - EPS + maxVertIdx;
                this._vertAlign.css({ left: tmp + 'px' });
                ret.x = tmp - left;
            }
            else {
                tmp = right - EPS + maxVertIdx2;
                this._vertAlign.css({ left: tmp + 'px' });
                ret.x = tmp - right;
            }

            if(Math.abs(ret.x) < eps) 
                this._vertAlign.addClass('line-correct');
            else
                this._vertAlign.removeClass('line-correct');
        }
        else {
            this._vertAlign.css({ left: '-500px' });
        }

        return ret;
    }

    hideAlignment() {
        this._horzAlign.css({ top: '-500px' });
        this._vertAlign.css({ left: '-500px' });
    }

    moveSelectingElements(delta, ignoreElement) {
        let len = this._lstSelectedElems.length;
        for(let i = 0; i < len; i++) {
            if(this._lstSelectedElems[i]._idx == ignoreElement._idx)
                continue;
            this._lstSelectedElems[i].onMoveInactive(delta);
        }
    }

    cloneSelectingElements(delta, ignoreElement) {
        let len = this._lstSelectedElems.length;
        // let listCloned = new Array(len);
        let nextIdx = this._nextIdx;
        let elem, newElem;

        console.log('cloneSelectingElements, selected count: ' + len + ', delta: ', delta);
        
        for(let i = 0; i < len; i++) {
            elem = this._lstSelectedElems[i];
            let elemInfo = elem.genElementInfo();
            console.log('idx: ' + nextIdx + ', type: ' + elem.getType() + ', info: ', elemInfo);

            newElem = this.createFormElement(elem.getType(), elemInfo, nextIdx);
            this._lstElements.push(newElem);

            newElem.render4Editing(this._container);
            newElem.onMoveInactive(delta);

            this._lstSelectedElems[i] = newElem;
            if(elem._idx != ignoreElement._idx)
                elem.onUnSelect();
            newElem.showCloneState();

            nextIdx++;
        }
        this._nextIdx = nextIdx;
    }

    showClonedElemsSelected() {
        let len = this._lstSelectedElems.length;
        console.log('showClonedElemsSelected, selected count: ' + len);
        for(let i = 0; i < len; i++) {
            let elem = this._lstSelectedElems[i];
            elem.onSelect(null);
        }
    }

    onMouseDown(ev) {
        // console.log(ev);
        var _self = this;

        //get correct relative position
        let curOffset = this._container.offset();
        let x = ev.pageX - curOffset.left;
        let y = ev.pageY - curOffset.top;

        //show the selecting div
        this._selectionCtrl.css({
            display: 'block',
            top: x + 'px',
            left: y + 'px',
            width: '2px',
            height: '2px'
        });
        this._startSelectPos = {
            x: x,
            y: y
        };
        this._curSelection = {
            x: x,
            y: y,
            w: 2,
            h: 2
        };

        this._container.on('mousemove', function(ev) {
            console.log('main div mousemove');
            _self.onMouseMove(ev);
            ev.preventDefault();
        })
        .on('mouseup', function(ev) {
            console.log('main div mouseup');
            _self.onMouseUp(ev);
            ev.preventDefault();
        });
    }
    onMouseMove(ev) {
        //get correct relative position
        let curOffset = this._container.offset();
        let x = ev.pageX - curOffset.left;
        let y = ev.pageY - curOffset.top;

        if(x < this._startSelectPos.x) {
            this._curSelection.x = x;
            this._curSelection.w = this._startSelectPos.x - this._curSelection.x;
        }
        else {
            this._curSelection.x = this._startSelectPos.x;
            this._curSelection.w = x - this._curSelection.x;
        }

        if(y < this._startSelectPos.y) {
            this._curSelection.y = y;
            this._curSelection.h = this._startSelectPos.y - this._curSelection.y;
        }
        else {
            this._curSelection.y = this._startSelectPos.y;
            this._curSelection.h = y - this._curSelection.y;
        }

        this._selectionCtrl.css({
            left: this._curSelection.x + 'px',
            top: this._curSelection.y + 'px',
            width: this._curSelection.w + 'px',
            height: this._curSelection.h + 'px'
        });
        
    }
    onMouseUp(ev) {
        this._container.off('mousemove').off('mouseup');
        // console.log('_curSelection: (' + this._curSelection.x + ', ' + this._curSelection.y + ', ' + this._curSelection.w + ', ' + this._curSelection.h + ')');

        //select elements
        this.unSelectAll();
        let len = this._lstElements.length;
        for(let i = 0; i < len; i++) {
            let bound = this._lstElements[i]._bounding;
            // console.log('bounding: (' + bound.left + ', ' + bound.top + ', ' + bound.width + ', ' + bound.height + ')');
            if(bound.left >= this._curSelection.x && bound.top >= this._curSelection.y
                && bound.left + bound.width <= this._curSelection.x + this._curSelection.w
                && bound.top + bound.height <= this._curSelection.y + this._curSelection.h) {
                    this.selectElement(this._lstElements[i]);
                    this._lstElements[i].onSelect(null);
                }
            
        }

        //event updating UI again if no control selected
        if(this._lstSelectedElems.length == 0)
            this.updateUIElementData();

        //hide the selecting div
        this._selectionCtrl.css({
            display: 'none'
        });
    }

    styleSelectingElems(prop, val) {
        let len = this._lstSelectedElems.length;
        for(let i = 0; i < len; i++) {
            this._lstSelectedElems[i].setStyle(prop, val);
        }

        //update current style
        this._curStyles[prop] = val;
    }

    removeStyleSelectingElems(prop) {
        let len = this._lstSelectedElems.length;
        for(let i = 0; i < len; i++) {
            this._lstSelectedElems[i].removeStyle(prop);
        }

        //update current style
        this._curStyles[prop] = undefined;
    }

    updateUIElementData() {
        if(this._lstSelectedElems.length != 1) {
            $('#divElementData').hide();
            $('#divImageOption').hide();
            return;
        }
        let elem = this._lstSelectedElems[0];
        let type = elem.getType();

        //common styles
        let style = elem.getStyle('font-size');
        if(style != undefined) {
            let s = style.substr(0, style.length - 2);
            $('#txtFontSize').val(s);
        }
        else {
            $('#txtFontSize').val('');
        }

        style = elem.getStyle('font-family');
        if(style != undefined) {
            $('#txtFontFamily').val(style);
        }
        else {
            $('#txtFontFamily').val('');
        }

        style = elem.getStyle('padding-top');
        if(style != undefined) {
            let s = style.substr(0, style.length - 2);
            $('#txtPaddingVert').val(s);
        }
        else {
            $('#txtPaddingVert').val('');
        }

        style = elem.getStyle('padding-left');
        if(style != undefined) {
            let s = style.substr(0, style.length - 2);
            $('#txtPaddingHorz').val(s);
        }
        else {
            $('#txtPaddingHorz').val('');
        }

        //specific data
        if(type == Constants.ELEMENT_TYPE.LABEL) {
            $('#divElementData').hide();
            $('#divImageOption').hide();
            return;
        }

        if(elem.getType() == Constants.ELEMENT_TYPE.COMMON_INFO) {
            $('#divElementData').show();
            $('#divImageOption').hide();

            $("#selectOTs").prop('disabled', 'disabled');
            $("#selectCommonInfos").prop('disabled', false);

            //show string common info
            this._fetchListCommonInfos(Constants.COMMON_INFO_TYPE.STRING);

            $("#selectCommonInfos").val(elem._commonInfo.defaultField);
        }
        else if(elem.getType() == Constants.ELEMENT_TYPE.TEXTBOX) { 
            $('#divElementData').show();
            $('#divImageOption').hide();

            $("#selectOTs").prop('disabled', false);
            $("#selectCommonInfos").prop('disabled', 'disabled');

            $("#selectOTs").val(elem._observationType.id);
        }
        else if(elem.getType() == Constants.ELEMENT_TYPE.IMAGE) { 
            $('#divElementData').hide();
            $('#divImageOption').show();

            $("#" + this._selectImageOptsName).val(elem._imageSizeOpt);
        }
        else if(elem.getType() == Constants.ELEMENT_TYPE.IMAGE_DATA) { 
            $('#divElementData').show();
            $('#divImageOption').show();

            $("#" + this._selectImageOptsName).val(elem._imageSizeOpt);

            $("#selectOTs").prop('disabled', 'disabled');
            $("#selectCommonInfos").prop('disabled', false);

            //show image common info
            this._fetchListCommonInfos(Constants.COMMON_INFO_TYPE.IMAGE);

            $("#selectCommonInfos").val(elem._commonInfo.defaultField);
        }
    }

    fetchListOTs(selectElemId, lstOTsJson) {
        Object.assign(GlobalVars.lstOTsData, lstOTsJson);
        this._selectOTsName = selectElemId;

        this._fetchListOTs(Constants.DATA_TYPE.NUMBER);

        let _self = this;
        $('#' + selectElemId).on('change', function(ev) {
            _self.onObservationTypeChanged(ev);
        });
    }

    fetchListCommonInfos(selectElemId, lstOTsJson) {
        Object.assign(GlobalVars.lstCommonInfosData, lstOTsJson);
        this._selectCommonInfosName = selectElemId;

        this._fetchListCommonInfos(Constants.COMMON_INFO_TYPE.STRING);

        let _self = this;
        $('#' + this._selectCommonInfosName).on('change', function(ev) {
            _self.onCommonInfoChanged(ev);
        });
    }

    fetchImageOptions(selectElemId) {
        var html = '';
        html += '<option value="' + Constants.IMAGE_SIZE_OPTION.ORIGINAL + '">Original size</option>';
        html += '<option value="' + Constants.IMAGE_SIZE_OPTION.STRETCH + '">Stretch image</option>';
        html += '<option value="' + Constants.IMAGE_SIZE_OPTION.FIT + '">Fit image</option>';

        let _self = this;
        $('#' + selectElemId).append(html);
        $('#' + selectElemId).on('change', function(ev) {
            _self.onImageOptionChanged(ev);
        });

        this._selectImageOptsName = selectElemId;
    }

    _fetchListCommonInfos(type) {
        var html = '';
        let len = GlobalVars.lstCommonInfosData.length;
        for (let i = 0; i < len; i++) {
            let obj = GlobalVars.lstCommonInfosData[i];
            if(obj['type'] == type)
                html += '<option value="' + obj['defaultField'] + '">' + obj['name'] + '</option>';
        }
        
        $('#' + this._selectCommonInfosName).html(html);
    }

    _fetchListOTs(type) {
        var html = '';
        let len = GlobalVars.lstOTsData.length;
        for (let i = 0; i < len; i++) {
            let obj = GlobalVars.lstOTsData[i];
            if(obj['type'] == type)
                html += '<option value="' + obj['id'] + '">' + obj['name'] + '</option>';
        }

        $('#' + this._selectOTsName).html(html);
    }

    onObservationTypeChanged(ev) {
        // console.log(ev);

        //safety check
        if(this._lstSelectedElems.length != 1) {
            return;
        }
        let elem = this._lstSelectedElems[0];
        if(elem.getType() == Constants.ELEMENT_TYPE.LABEL) {
            return;
        }

        let selectedId = $("#" + this._selectOTsName).val();
        let selectedOT = GlobalVars.lstOTsData.find((en) => en.id == selectedId);
        if(selectedOT == undefined) {
            console.error('DEV: why cannot find selectedOT??? selectedId: ' + selectedId);
            return;
        }
        elem.updateObservationType(selectedOT);
    }

    onCommonInfoChanged(ev) {
        // console.log(ev);

        //safety check
        if(this._lstSelectedElems.length != 1) {
            return;
        }
        let elem = this._lstSelectedElems[0];
        if(elem.getType() == Constants.ELEMENT_TYPE.LABEL) {
            return;
        }

        let selectedId = $("#" + this._selectCommonInfosName).val();
        let selectedCommonInfo = GlobalVars.lstCommonInfosData.find((en) => en.defaultField == selectedId);
        if(selectedCommonInfo == undefined) {
            console.error('DEV: why cannot find selectedCommonInfo??? selectedId: ' + selectedId);
            return;
        }
        elem.updateCommonInfo(selectedCommonInfo);
    }

    onImageOptionChanged(ev) {
        //safety check
        if(this._lstSelectedElems.length != 1) {
            return;
        }
        let elem = this._lstSelectedElems[0];
        if(elem.getType() != Constants.ELEMENT_TYPE.IMAGE) {
            return;
        }

        let selectedId = $("#" + this._selectImageOptsName).val();
        elem.setImageSizeOption(parseInt(selectedId));
    }

    onImageUrlChanged(url) {
        //safety check
        if(this._lstSelectedElems.length != 1) {
            return;
        }
        let elem = this._lstSelectedElems[0];
        if(elem.getType() != Constants.ELEMENT_TYPE.IMAGE) {
            return;
        }

        elem.setImageSrcUrl(url);
    }

    setDefaultElementValue(type, elementInfo) {
        if(type == Constants.ELEMENT_TYPE.LABEL) {
            elementInfo.value = 'Label';
        }
        else if(type == Constants.ELEMENT_TYPE.COMMON_INFO) {
            if(GlobalVars.lstCommonInfosData.length > 0) {
                for(let i = 0; i < GlobalVars.lstCommonInfosData.length; i++) {
                    if(GlobalVars.lstCommonInfosData[i].type == Constants.COMMON_INFO_TYPE.STRING) {
                        let item = GlobalVars.lstCommonInfosData[i];
                        elementInfo.name = item.name;
                        elementInfo.defaultField = item.defaultField;
                        break;
                    }
                }
            }
            else {
                elementInfo.name = 'Common info';
                elementInfo.defaultField = 'empty';
            }
        }
        else if(type == Constants.ELEMENT_TYPE.TEXTBOX) {
            elementInfo.observationType = {};
            if(GlobalVars.lstOTsData.length > 0) {
                let item = GlobalVars.lstOTsData[0];
                Object.assign(elementInfo.observationType, item);
                elementInfo.value = item.name;
            }
        }
        else if(type == Constants.ELEMENT_TYPE.IMAGE_DATA) {
            if(GlobalVars.lstCommonInfosData.length > 0) {
                for(let i = 0; i < GlobalVars.lstCommonInfosData.length; i++) {
                    if(GlobalVars.lstCommonInfosData[i].type == Constants.COMMON_INFO_TYPE.IMAGE) {
                        let item = GlobalVars.lstCommonInfosData[i];
                        elementInfo.name = item.name;
                        elementInfo.defaultField = item.defaultField;
                        break;
                    }
                }
            }
            else {
                elementInfo.name = 'Common info';
                elementInfo.defaultField = 'empty';
            }
        }
    }

    saveForm() {
        //check name & title
        let title = $('#txtFormTitle').val();
        title = title.trim();
        if(title === '') {
            $.alert({
                title: 'Cảnh báo',
                content: 'Hãy nhập tên form trước khi lưu!',
            });
            return;
        }
        let priority = $('#txtPriority').val();
        if(priority === '') {
            $.alert({
                title: 'Cảnh báo',
                content: 'Hãy nhập thứ tự ưu tiên trước khi lưu!',
            });
            return;
        }
        this._title = title;
        this._priority = priority;
    
        //check elements
    
        //generate data
        let data = {
            name: title,
            priority: priority,
            pages: [],
            elements: ''
        };

        //id
        if(GlobalVars.formId !== Constants.DEFAULT_NEW_FORM_ID) {
            data['id'] = GlobalVars.formId;
        }

        //pages
        for(let i = 0; i < this._lstPages.length; i++) {
            let obj = {};
            Object.assign(obj, this._lstPages[i]);
            data.pages.push(obj);
        }

        //elements
        let len = this._lstElements.length;
        let elem;
        let arrElems = [];
        for(let i = 0; i < len; i++) {
            elem = this._lstElements[i];
            arrElems.push(elem.genElementInfo());
        }
        data.elements = JSON.stringify(arrElems);
    
        //call to parent
        parent.saveForm(data, function(res) {
            console.log('saveForm response:', res);
            if(res.isValid == true) {
                $.dialog({
                    title: 'Thành công',
                    content: 'Lưu mẫu in thành công!',
                });
                GlobalVars.formId = res.id;
            }
            else {
                let msg = 'N/A';
                if(res.error != null && res.error != undefined) {
                    msg = res.error.errorMessage;
                }
                $.alert({
                    title: 'Lưu thất bại',
                    content: 'Lưu mẫu in thất bại do lỗi: "' + msg + '" !',
                });
            }
        });
    }
 }