import Constants from './constants';
import FormViewer from './formviewer';
import GlobalVars from './globalvars';

function getParams(name) {
	if (name = (new RegExp('[?&]' + encodeURIComponent(name) + '=([^&]*)')).exec(location.search))
		return decodeURIComponent(name[1]);
}

let sDomain = getParams("domain");
if(sDomain != undefined)
	GlobalVars.domain = sDomain;
console.log("API: " + GlobalVars.domain);

//original formId
let s = getParams("formId");
if(s != undefined)
    GlobalVars.formId = s;
console.log("formId: " + GlobalVars.formId);

//user info
s = getParams("userId");
if(s != undefined)
    GlobalVars.userId = s;
s = getParams("username");
if(s != undefined)
    GlobalVars.username = s;
console.log("user: " + GlobalVars.username + ', ' + GlobalVars.userId);

s = getParams("hasOT");
if(s != undefined)
    GlobalVars.hasOTs = (s === 'true') ? true : false;

///////////////////////////////////////////////////////////////////////////////

var mainForm = null;
var mainDiv = null;

export function initFormEditing(containerId) {
    console.log('initFormEditing');

    //get form info
    if(GlobalVars.formId !== Constants.DEFAULT_NEW_FORM_ID) {
        parent.getFormData(GlobalVars.formId, function(res) {
            console.log(res);
            showFormEditing(containerId, res);
        });
    }
    else {
        console.log('new empty form');
        showFormEditing(containerId, []);
    }

    //btns & inputs
    $("#txtFontSize").on('change', function(ev) {
        // console.log('onFontSize changed', ev);
        mscform.styleFontSize(ev.currentTarget.value);
    });
    $("#txtFontFamily").on('change', function(ev) {
        // console.log('onFontFamily changed', ev);
        mscform.styleFontFamily(ev.currentTarget.value);
    });
    $("#txtPaddingVert").on('change', function(ev) {
        mscform.stylePaddingVert(ev.currentTarget.value);
    });
    $("#txtPaddingHorz").on('change', function(ev) {
        mscform.stylePaddingHorz(ev.currentTarget.value);
    });
    $("#txtImageUrl").on('change', function(ev) {
        // console.log('onFontFamily changed', ev);
        mscform.onImageUrlChanged(ev.currentTarget.value);
    });

    console.log('initFormEditing end');
}

export function showFormEditing(containerId, lstElements) {
    if(mainForm != null)
        mainForm.dispose();

    mainDiv = $('#' + containerId);
    mainForm = new FormViewer(mainDiv, lstElements);
    mainForm.render4Editing(mainDiv);

    //list common infos & OTs
    parent.getListCommonInfos(function(res) {
        fetchListCommonInfos('selectCommonInfos', res);
    });
    if(GlobalVars.hasOTs) {
        parent.getListOTs(function(res) {
            fetchListOTs('selectOTs', res);
        });
    }
                
    fetchImageOptions('selectImageOptions');
}

export function initFormOthers(containerId, lstElements, isPrintOrInput, commonInfoData) {
    console.log('initFormOthers');
    if(mainForm != null)
        mainForm.dispose();

    mainDiv = $('#' + containerId);
    mainForm = new FormViewer(mainDiv, lstElements);

    //TODO: set commonInfoData values

    if(isPrintOrInput)
        mainForm.render4Printing(mainDiv);
    else
        mainForm.render4Input(mainDiv);
}

export function showFormPrinting(containerId) {
    let div = $('#' + containerId);
    mainForm.render4Printing(div);
}

export function showFormViewing(containerId) {
    let div = $('#' + containerId);
    mainForm.render4Input(div);
}

export function saveForm() {
    mainForm.saveForm();
}

